body {
  background-image: url(./components/background.jpg);
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
nav {
  position: absolute;
  background-color: rgb(1, 1, 34);
  display: flex;
  color: #fff;
  position: fixed;
  width: 100%;
  height: 13%;
  top: 0;
  z-index: 1000;
  box-shadow: 3px 3px 4px black;
}
nav > h1 {
  text-shadow: 5px 5px 2px black;
  text-decoration: none;
  font-size: 1.8em;
}
nav .nav-cont {
  position: relative;
  list-style-type: none;
  display: flex;
  gap: 4em;
  align-items: end;
  width: 80%;
  justify-content: flex-end;
  text-decoration: none;
}
.nav-cont .icon {
  width: 2em;
  height: 1.5em;
  text-rendering: none;
}
.icon:hover {
  background-color: white;
  transform: translateY(-30%);
  border-radius: 50%;
}
.icon:active {
  background: white;
}

/* Home page */
.Home {
  margin-top: 6em;
  position: relative;
  display: flex;
  justify-content: flex-start;
}
.basic {
  margin-left: 2em;
  width: 50%;
  color: aqua;
}
.basic h1,
h2,
h3,
p,
li {
  text-shadow: 5px 5px 1px black;
}
.basic button {
  width: 20%;
  height: 12%;
  color: white;
  text-shadow: 0px 1px 1px white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  background-color: rgb(2, 2, 41);
  animation: animate alternate 3s ease-in;
}
button:hover {
  color: aqua;
  font-size: 12px;
  text-decoration: solid;
  background-color: blue;
}
.img {
  position: relative;
  width: 45%;
  background-image: url(./components/languages.png);
  height: 25em;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 3px -2px 5px black;
  border-radius: 10px;
  box-shadow: 10px 10px 15px aqua;
}
.img p {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  text-align: center;
  text-shadow: 2px 1px 2px aqua;
  font-size: 1em;
}
/*About*/
.About {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
}
.card1 {
  margin-top: 6em;
  width: 100%;
  height: 25em;
  background-image: url(./components/campus.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  justify-content: center;
  border-radius: 30px;
  box-shadow: 10px -10px 15px red;
}
.card2 {
  margin-top: 1em;
  width: 90%;
  height: 25em;
  background-image: url(./components/code.png);
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  justify-content: center;
  border-radius: 30px;
  box-shadow: 10px -10px 15px red;
}
.card3 {
  display: flex;
  justify-content: flex-start;
  margin-top: 1em;
  width: 70%;
  height: 25em;
  background-image: url(./components/2345ty.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  justify-content: center;
  border-radius: 30px;
  box-shadow: 10px -10px 15px red;
}
/*Small screen*/
@media only screen and (max-width: 600px) {
  nav {
    height: 7%;
  }
  nav > h1 {
    font-size: 1.2em;
  }
  nav .nav-cont {
    width: 80%;
    gap: 2em;
  }
  .nav-cont .icon {
    width: 1em;
    height: 1em;
  }
  .icon:hover {
    background-color: white;
    transform: translateY(-20%);
    border-radius: 50%;
    box-shadow: 10px 10px 15px blue;
  }
  .Home {
    width: 100%;
    margin-top: 4em;
    flex-direction: column;
    position: relative;
    justify-content: center;
  }
  .img {
    order: 1;
    width: 100%;
    height: 16em;
  }
  .basic {
    margin-left: 1em;
    width: 100%;
    color: aqua;
    order: 2;
  }
  .basic button {
    width: 40%;
    height: 4em;
  }
}
